import React, { Component } from 'react';

class CheckboxesElement extends Component {

  constructor(props) {
    super(props);

    this.state = {
      label: "Checkboxes",
      options: ["Option 1", "Option 2", "Option 3"]
    };
  }

  render() {
    return (
      <div>
        <label className="editable editable-label">{this.state.label}</label>
        <div className="form-element">
            <div className="outer">
              {this.state.options.map(function (option) {
                return <div className="checkbox">
                  <label><input type="checkbox" value="" />{option}</label>
                </div>
              })}
            </div>
        </div>
      </div>
    );
  }
}

export default CheckboxesElement;


