import React, { Component } from 'react';
import './Nav.css';

class Nav extends Component {
  render() {
    return (
      <nav className="site-nav">
        <div className="container">
            <div className="row">
                <div className="col-sm-3">
                    &nbsp;
                </div>
                <div className="col-sm-6 text-center">
                    <ul>
                        <li><a>Build</a></li>
                        <li><a>Settings</a></li>
                        <li><a>Publish</a></li>
                    </ul>
                </div>
                <div className="col-sm-3">
                    &nbsp;
                </div>
            </div>
        </div>
      </nav>
    );
  }
}

export default Nav;


