import React, { Component } from 'react';
import classNames from 'classnames';
import $ from 'jquery';
import 'jquery-ui/ui/widgets/draggable.js';
import './Sidebar.css';

class Sidebar extends Component {
  
  constructor(props) {
    super(props);

    this.state = {
      showAddFormTab: true,
      showFormElements: false
    };

    this.toggleSidebarElements = this.toggleSidebarElements.bind(this);

    this.formElementsClass = classNames(
      'form-elements',
      {'form-elements-show': this.state.showFormElements }
    );
  }

  toggleSidebarElements() {
    this.setState({
        showAddFormTab: !this.state.showAddFormTab,
        showFormElements: !this.state.showFormElements
    });

    this.formElementsClass = classNames(
      'form-elements',
      {'form-elements-show': !this.state.showFormElements }
    );
  }

  componentDidMount() {
    var that = this;

    $(".element-container").draggable({
        opacity: 0.7,
        helper: "clone",
        connectToSortable: ".sortable"
    });

    this.setHeight();

    $(window).resize(function(){
        that.setHeight();
    });
  }

  setHeight() {

    var height = $(window).height();
    var offset = $('.sortable-container').offset().top;

    height = height - offset;
    $('.sortable-container').css({'height': height});
    $('.form-elements').css({'height': height});
    $('.element-properties').css({'height': height});

  }

  render() {
    return (
      <div>
        { this.state.showAddFormTab &&
          <div onClick={this.toggleSidebarElements} className="add-form">
            <div className="add-form-text">
                Add Form Element
            </div>
            <div className="circle-normal"><span className="glyphicon glyphicon-plus"></span></div>
            
            <div className="circle-ripple circle-ripple-1"></div>
            <div className="circle-ripple circle-ripple-2"></div>
          </div>
        }
      <div className={this.formElementsClass}>
        <div className="element-main-header">Form Elements
        <span onClick={this.toggleSidebarElements} className="glyphicon glyphicon-remove pull-right form-elements-remove"></span>
        </div>

        <div id="header" className="element-container">
            <div className="element-icon">
                <span className="glyphicon glyphicon-header"></span>
            </div>
            <div className="element-text">
                Header
            </div>
        </div>
        <div id="name" className="element-container">
            <div className="element-icon">
                <span className="glyphicon glyphicon-user"></span>
            </div>
            <div className="element-text">
                Full Name
            </div>
        </div>
        <div id="email" className="element-container">
            <div className="element-icon">
                <span className="glyphicon glyphicon-envelope"></span>
            </div>
            <div className="element-text">
                Email
            </div>
        </div>
        <div id="address" className="element-container">
            <div className="element-icon">
                <span className="glyphicon glyphicon-map-marker"></span>
            </div>
            <div className="element-text">
                Address
            </div>
        </div>
        <div id="input" className="element-container">
            <div className="element-text">
                Input
            </div>
        </div>
        <div id="textarea" className="element-container">
            <div className="element-text">
                Textarea
            </div>
        </div>
        <div id="checkboxes" className="element-container">
            <div className="element-text">
                Checkboxes
            </div>
        </div>
        <div id="radio_buttons" className="element-container">
            <div className="element-text">
                Radio Buttons
            </div>
        </div>
        <div id="select" className="element-container">
            <div className="element-text">
                Select
            </div>
        </div>
      </div>
      </div>
    );
  }
}

export default Sidebar;


