import React, { Component } from 'react';
import './Header.css';

class Header extends Component {
  render() {
    return (
      <header>
        <div className="col-sm-8">
          <a className="logo">Bootstrap Form Builder</a>
        </div>
        <div className="col-sm-4 text-right">
          <a href="https://github.com/GregFrench/Bootstrap-Form-Builder" target="_blank" rel="noopener noreferrer">GitHub</a>
        </div>
      </header>
    );
  }
}

export default Header;


