import React, { Component } from 'react';
import classNames from 'classnames';
import $ from 'jquery';
import './Element.css';
import HeaderElement from './elements/HeaderElement.js';
import EmailElement from './elements/EmailElement.js';
import AddressElement from './elements/AddressElement.js';
import NameElement from './elements/NameElement.js';
import InputElement from './elements/InputElement.js';
import TextareaElement from './elements/TextareaElement.js';
import CheckboxesElement from './elements/CheckboxesElement.js';
import RadioButtonsElement from './elements/RadioButtonsElement.js';
import SelectElement from './elements/SelectElement.js';

class Element extends Component {

  constructor(props) {
    super(props);

    this.state = {
        isFocused: false
    }

    this.elementFocus = this.elementFocus.bind(this, this.props.index);
  }

  elementFocus(index) {
    this.setState({
      isFocused: !this.state.isFocused
    });

    this.props.actions.focusField(index);
  }

  elementFocusOut(index) {
    this.setState({
      isFocused: false
    });

    this.props.actions.focusField(-1);
  }

  componentDidMount() {
    var that = this;

    $('body').click(function (evt) {
      if (evt.target.className === "form-element-container" ||
        evt.target.className === "element-properties") {
        return;
      }

      //For descendants of "form-element-container" being clicked, remove this check if you do not want to put constraint on descendants.
      if ($(evt.target).closest('.form-element-container').length ||
        $(evt.target).closest('.element-properties').length) {
        return;
      }         

      //Do processing of click event here for every element except with classname 'form-element-container'
      //that.fields.forEach(function (field) {

        that.elementFocusOut(that.props.index)
        //that.showElementProperties = false;

      //});

    });
  }

  render() {
    if (this.props.fields[this.props.index] !== undefined) {
      this.elementClass = classNames(
        'form-group',
        'form-element-container',
        {'focused-element': this.props.fields[this.props.index].isFocused}
      );
    }

    return (
      //<div onClick={this.elementFocus} tabIndex="-1" className={this.elementClass}>
      <div onClick={this.elementFocus} tabIndex="-1" className={this.elementClass}>
        { this.props.name === "header" &&
          <HeaderElement field={this.props.fields[this.props.index]} />
        }
        { this.props.name === "email" &&
          <EmailElement field={this.props.fields[this.props.index]} />
        }
        { this.props.name === "address" &&
          <AddressElement field={this.props.fields[this.props.index]} />
        }
        { this.props.name === "name" &&
          <NameElement field={this.props.fields[this.props.index]} />
        }
        { this.props.name === "input" &&
          <InputElement field={this.props.fields[this.props.index]}/>
        }
        { this.props.name === "textarea" &&
          <TextareaElement field={this.props.fields[this.props.index]} />
        }
        { this.props.name === "checkboxes" &&
          <CheckboxesElement field={this.props.fields[this.props.index]} />
        }
        { this.props.name === "radio_buttons" &&
          <RadioButtonsElement field={this.props.fields[this.props.index]} />
        }
        { this.props.name === "select" &&
          <SelectElement field={this.props.fields[this.props.index]} />
        }
      </div>
    );
  }
}

export default Element;


