import React, { Component } from 'react';

class EmailElement extends Component {

  constructor(props) {
    super(props);

    this.state = {
      label: "Email"
    };
  }

  render() {
    return (
      <div>
        <label className="editable editable-label">{this.state.label}</label>
        <input type="email" name="email" className="form-control" placeholder="email" />
      </div>
    );
  }
}

export default EmailElement;


