import React, { Component } from 'react';

class TextareaElement extends Component {

  constructor(props) {
    super(props);

    this.state = {
      label: "Textarea"
    };
  }

  render() {
    return (
      <div>
        <label className="editable">{this.state.label}</label>
        <textarea className="form-control"></textarea>
      </div>
    );
  }
}

export default TextareaElement;


