import React, { Component } from 'react';
import classNames from 'classnames';
import $ from 'jquery';
import 'jquery-ui/ui/widgets/sortable.js';
import './Sortable.css';
import Element from './Element.js';

class Sortable extends Component {

  constructor(props) {
    super(props);

    this.state = {
      fields: []
    };

  }

  swap(array, first, second){
    var final = array.slice();
    var temp = final[first];
    final[first] = final[second];
    final[second] = temp;
    return final
  }

  componentDidMount() {
    var that = this;

    $('.sortable').sortable({
      axis: "y",
      cancel: '.editable',
      start: function(e, ui) {

        // creates a temporary attribute on the element with the old index
        $(this).attr('data-previndex', ui.item.index());

      },
      receive: function (event, ui) {
        if (ui.item.attr("id")) {

            var newIndex = parseInt($(this).data("ui-sortable").currentItem.index(), 10),
                element_id = ui.item.attr("id");

            $(this).removeAttr('data-previndex');
            $(ui.helper).replaceWith("");
            
            let arr = that.state.fields.slice(0);
            arr.splice(newIndex, 0, element_id);

            that.setState({
              fields: arr
            });

            that.props.actions.addField({
              label: element_id,
              type: element_id,
              isFocused: false,
              index: that.props.fields.length
            });

            //that.elementFocus(newIndex);

        }

      },
      update: function (event, ui) {

        /*if (ui.item.index() !== -1) {
          var newIndex = ui.item.index(),
              oldIndex = parseInt($(this).attr('data-previndex'), 10);
          
          $(this).removeAttr('data-previndex');

          $(ui.helper).replaceWith("");

          that.setState({
            fields: that.state.fields
          });*/

          //let arr = that.props.fields.slice(0);
          //arr = that.swap(arr, newIndex, oldIndex);

          //that.props.actions.addField(arr);

          //that.elementFocus(newIndex);
        //}
      }
    });
  }

  render() {

    this.sortableClass = classNames(
      'sortable',
      {'sortable-border': this.props.fields.length === 0}
    );

    var that = this;

    return (
      <div className="sortable-container">
        <div className={this.sortableClass}>
          {this.state.fields.map(function (field,index) {
            return <Element name={field} key={index} index={index} fields={that.props.fields} actions={that.props.actions} />;
          })}
        </div>
      </div>
    );
  }
}

export default Sortable;


