import React from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Header from '../Header.js';
import Nav from '../Nav.js';
import Sidebar from '../Sidebar.js';
import Sortable from '../Sortable.js';
import PropertiesPanel from '../PropertiesPanel.js';
import * as FieldActions from '../actions'
import '../App.css';

const App = ({fields, actions}) => (
  <div className="App">
    <Header />
    <Nav />
    <div className="content-container build-container build-body">
      <Sidebar />
      <Sortable fields={fields.fields} actions={actions} />
      <PropertiesPanel fields={fields} actions={actions} />
    </div>
  </div>
)

App.propTypes = {
  fields: PropTypes.object.isRequired,
  actions: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
  fields: state.fields
});

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(FieldActions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);


