import React, { Component } from 'react';

class HeaderElement extends Component {

  constructor(props) {
    super(props);

    this.state = {
      label: "Header"
    };
  }

  render() {

    let label = this.props.field === undefined ? this.state.label : this.props.field.label;

    return (
      <h1>
        <span className="editable editable-label">{label}</span><br />
      </h1>
    );
  }
}

export default HeaderElement;


