import * as types from '../constants/ActionTypes'

export const addField = obj => ({ type: types.ADD_FIELD, obj })
export const deleteField = id => ({ type: types.DELETE_FIELD, id })
//export const updateActiveIndex = index => ({ type: types.UPDATE_ACTIVE_INDEX, index })
export const updateField = (obj, index) => ({ type: types.UPDATE_FIELD, obj, index })
export const focusField = index => ({ type: types.FOCUS_FIELD, index })


