import { ADD_FIELD, DELETE_FIELD, UPDATE_FIELD, FOCUS_FIELD } from '../constants/ActionTypes'

const initialState = {
  activeIndex: null,
  fields: []
}

export default function field(state = initialState, action) {
  switch (action.type) {
    case ADD_FIELD:
      return {...state, fields: [...state.fields, action.obj]}

    case UPDATE_FIELD:
      return {...state, fields: state.fields.map( (item, index) => {
        if(index !== action.index) {
          // This isn't the item we care about - keep it as-is
          return item;
        }

        // Otherwise, this is the one we want - return an updated value
        return {
          ...item,
          ...action.obj
        };  
      })};

    case DELETE_FIELD:
      return state.fields.filter(field =>
        field.id !== action.id
      )

    case FOCUS_FIELD:
      return {...state,
        activeIndex: action.index, 
        fields: state.fields.map(field =>
          field.index === action.index ?
            { ...field, isFocused: true } :
            { ...field, isFocused: false }
      )};

    default:
      return state
  }
}


