import React, { Component } from 'react';

class NameElement extends Component {

  constructor(props) {
    super(props);

    this.state = {
      label: "Name"
    };
  }

  render() {
    return (
      <div>
        <label className="editable editable-label">{this.state.label}</label>
        <div className="form-group row">
          <div className="col-xs-3">
            <input type="text" className="form-control" name="prefix" size="4" placeholder="prefix" />
            <span className="help-block">prefix</span>
          </div>
          <div className="col-xs-3">
            <input type="text" className="form-control" name="first_name" placeholder="first name" />
            <span className="help-block">first name</span>
          </div>
          <div className="col-xs-3">
            <input type="text" className="form-control" name="last_name" placeholder="last name" />
            <span className="help-block">last name</span>
          </div>
          <div className="col-xs-3">
            <input type="text" className="form-control" name="suffix" size="4" placeholder="suffix" />
            <span className="help-block">suffix</span>
          </div>
        </div>
      </div>
    );
  }
}

export default NameElement;


